/**
 * ÈÖçÁΩÆÂêëÂØº
 * Â∏ÆÂä©Áî®Êà∑ÈÄâÊã©ÊúÄÈÄÇÂêàÁöÑÈÖçÁΩÆ
 */

import type { PresetName } from '../config/presets'
import { presetDescriptions } from '../config/presets'
import { getComponentFactory } from './base/ComponentFactory'
import { Modal } from './base/Modal'

/**
 * ‰ΩøÁî®Âú∫ÊôØÈóÆÈ¢ò
 */
interface WizardQuestion {
  id: string
  question: string
  options: Array<{
    label: string
    value: string
    description?: string
  }>
}

/**
 * ÈÖçÁΩÆÂêëÂØºÁ±ª
 */
export class ConfigWizard {
  private componentFactory = getComponentFactory()
  private modal: Modal | null = null
  private answers: Record<string, string> = {}
  private currentStep: number = 0

  private questions: WizardQuestion[] = [
    {
      id: 'usage',
      question: '‰Ω†‰∏ªË¶ÅÁî®ÁºñËæëÂô®ÂÅö‰ªÄ‰πàÔºü',
      options: [
        { label: 'üìù ÂÜôÂçöÂÆ¢ÊñáÁ´†', value: 'blog', description: '‰∏™‰∫∫ÂçöÂÆ¢„ÄÅÊñáÁ´†ÂÜô‰Ωú' },
        { label: 'üè¢ ÁÆ°ÁêÜÂÜÖÂÆπ', value: 'cms', description: '‰ºÅ‰∏öCMS„ÄÅÂÜÖÂÆπÂèëÂ∏É' },
        { label: 'üë• Âõ¢ÈòüÂçè‰Ωú', value: 'collaboration', description: 'Â§ö‰∫∫Âú®Á∫øÁºñËæë' },
        { label: 'üìÑ ÂÜôÊäÄÊúØÊñáÊ°£', value: 'markdown', description: 'Markdown„ÄÅAPIÊñáÊ°£' },
        { label: 'üìí ËÆ∞Á¨îËÆ∞', value: 'note', description: '‰∏™‰∫∫Á¨îËÆ∞„ÄÅÁü•ËØÜÁÆ°ÁêÜ' },
        { label: 'üí¨ ËØÑËÆ∫ÂõûÂ§ç', value: 'comment', description: 'ËØÑËÆ∫Ê°Ü„ÄÅË°®Âçï' },
      ],
    },
    {
      id: 'features',
      question: '‰Ω†ÈúÄË¶ÅÂì™‰∫õÂäüËÉΩÔºü',
      options: [
        { label: 'üé® ‰∏∞ÂØåÁöÑÊ†ºÂºèÂåñ', value: 'rich-format' },
        { label: 'üñºÔ∏è ÂõæÁâáËßÜÈ¢ë', value: 'media' },
        { label: 'üìä Ë°®Ê†ºÊîØÊåÅ', value: 'table' },
        { label: 'ü§ñ AIËæÖÂä©', value: 'ai' },
        { label: '‚ö° Âè™Ë¶ÅÂü∫Á°ÄÂäüËÉΩÔºàÊõ¥Âø´Ôºâ', value: 'basic' },
      ],
    },
    {
      id: 'device',
      question: '‰∏ªË¶ÅÂú®‰ªÄ‰πàËÆæÂ§á‰∏ä‰ΩøÁî®Ôºü',
      options: [
        { label: 'üíª Ê°åÈù¢ÁîµËÑë', value: 'desktop' },
        { label: 'üì± ÊâãÊú∫Âπ≥Êùø', value: 'mobile' },
        { label: 'üåê ‰∏§ËÄÖÈÉΩÊúâ', value: 'both' },
      ],
    },
    {
      id: 'priority',
      question: '‰Ω†Êõ¥ÁúãÈáç‰ªÄ‰πàÔºü',
      options: [
        { label: '‚ö° ÈÄüÂ∫¶ÊÄßËÉΩ', value: 'performance' },
        { label: 'üé® ÂäüËÉΩ‰∏∞ÂØå', value: 'features' },
        { label: '‚öñÔ∏è Âπ≥Ë°°‰∏§ËÄÖ', value: 'balanced' },
      ],
    },
  ]

  /**
   * ÊòæÁ§∫ÂêëÂØº
   */
  show(onComplete: (preset: PresetName) => void): void {
    this.answers = {}
    this.currentStep = 0

    this.modal = new Modal({
      title: 'ÈÖçÁΩÆÂêëÂØº',
      width: 800,
    })

    this.renderStep()
    this.modal.show()

    // ‰øùÂ≠òÂõûË∞É
    this.onComplete = onComplete
  }

  private onComplete?: (preset: PresetName) => void

  /**
   * Ê∏≤ÊüìÂΩìÂâçÊ≠•È™§
   */
  private renderStep(): void {
    const question = this.questions[this.currentStep]
    const content = this.createStepContent(question)

    this.modal?.setContent(content)
    this.modal?.setFooter(this.createFooter())
  }

  /**
   * ÂàõÂª∫Ê≠•È™§ÂÜÖÂÆπ
   */
  private createStepContent(question: WizardQuestion): HTMLElement {
    const container = document.createElement('div')
    container.className = 'wizard-step'
    container.style.cssText = `
      padding: 30px;
      min-height: 300px;
    `

    // ËøõÂ∫¶ÊåáÁ§∫Âô®
    const progress = document.createElement('div')
    progress.style.cssText = `
      display: flex;
      justify-content: center;
      gap: 8px;
      margin-bottom: 30px;
    `

    for (let i = 0; i < this.questions.length; i++) {
      const dot = document.createElement('div')
      dot.style.cssText = `
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: ${i === this.currentStep ? '#3b82f6' : '#e5e7eb'};
        transition: all 0.3s;
      `
      progress.appendChild(dot)
    }

    container.appendChild(progress)

    // ÈóÆÈ¢òÊ†áÈ¢ò
    const title = document.createElement('h3')
    title.textContent = `${this.currentStep + 1}. ${question.question}`
    title.style.cssText = `
      font-size: 20px;
      font-weight: 600;
      margin-bottom: 24px;
      text-align: center;
      color: var(--editor-color-text-primary, #1f2937);
    `
    container.appendChild(title)

    // ÈÄâÈ°πÂàóË°®
    const options = document.createElement('div')
    options.className = 'wizard-options'
    options.style.cssText = `
      display: grid;
      gap: 12px;
    `

    question.options.forEach((option) => {
      const optionEl = document.createElement('div')
      optionEl.className = 'wizard-option'
      optionEl.style.cssText = `
        padding: 16px;
        border: 2px solid #e5e7eb;
        border-radius: 8px;
        cursor: pointer;
        transition: all 0.2s;
        background: white;
      `

      optionEl.innerHTML = `
        <div style="font-weight: 600; font-size: 16px; margin-bottom: 4px;">${option.label}</div>
        ${option.description ? `<div style="font-size: 14px; color: #6b7280;">${option.description}</div>` : ''}
      `

      optionEl.addEventListener('mouseenter', () => {
        optionEl.style.borderColor = '#3b82f6'
        optionEl.style.background = '#f0f9ff'
      })

      optionEl.addEventListener('mouseleave', () => {
        optionEl.style.borderColor = '#e5e7eb'
        optionEl.style.background = 'white'
      })

      optionEl.addEventListener('click', () => {
        this.answers[question.id] = option.value
        this.nextStep()
      })

      options.appendChild(optionEl)
    })

    container.appendChild(options)

    return container
  }

  /**
   * ÂàõÂª∫Â∫ïÈÉ®ÊåâÈíÆ
   */
  private createFooter(): HTMLElement {
    const footer = document.createElement('div')
    footer.style.cssText = `
      display: flex;
      justify-content: space-between;
      padding: 16px 30px;
      border-top: 1px solid var(--editor-color-border, #e5e7eb);
    `

    // ËøîÂõûÊåâÈíÆ
    const backBtn = this.componentFactory.createButton({
      label: '‰∏ä‰∏ÄÊ≠•',
      type: 'secondary',
      disabled: this.currentStep === 0,
      onClick: () => this.prevStep(),
    })

    // Ë∑≥ËøáÊåâÈíÆ
    const skipBtn = this.componentFactory.createButton({
      label: 'Ë∑≥ËøáÂêëÂØº',
      type: 'text',
      onClick: () => this.skip(),
    })

    footer.appendChild(backBtn)
    footer.appendChild(skipBtn)

    return footer
  }

  /**
   * ‰∏ã‰∏ÄÊ≠•
   */
  private nextStep(): void {
    if (this.currentStep < this.questions.length - 1) {
      this.currentStep++
      this.renderStep()
    }
    else {
      this.complete()
    }
  }

  /**
   * ‰∏ä‰∏ÄÊ≠•
   */
  private prevStep(): void {
    if (this.currentStep > 0) {
      this.currentStep--
      this.renderStep()
    }
  }

  /**
   * Ë∑≥ËøáÂêëÂØº
   */
  private skip(): void {
    this.modal?.hide()

    if (this.onComplete)
      this.onComplete('blog') // ÈªòËÆ§‰ΩøÁî®ÂçöÂÆ¢È¢ÑËÆæ
  }

  /**
   * ÂÆåÊàêÂêëÂØº
   */
  private complete(): void {
    // Ê†πÊçÆÁ≠îÊ°àÊé®ËçêÈÖçÁΩÆ
    const preset = this.recommendPreset()

    // ÊòæÁ§∫Êé®ËçêÁªìÊûú
    this.showRecommendation(preset)
  }

  /**
   * Êé®ËçêÈÖçÁΩÆ
   */
  private recommendPreset(): PresetName {
    const { usage, features, device, priority } = this.answers

    // Ê†πÊçÆ‰ΩøÁî®Âú∫ÊôØ
    if (usage === 'blog')
      return 'blog'
    if (usage === 'cms')
      return 'cms'
    if (usage === 'collaboration')
      return 'collaboration'
    if (usage === 'markdown')
      return 'markdown'
    if (usage === 'note')
      return 'note'
    if (usage === 'comment')
      return 'comment'

    // Ê†πÊçÆÂäüËÉΩÈúÄÊ±Ç
    if (features === 'ai')
      return 'aiEnhanced'
    if (features === 'basic' || priority === 'performance')
      return 'minimal'

    // Ê†πÊçÆËÆæÂ§á
    if (device === 'mobile')
      return 'mobile'

    // ÈªòËÆ§
    return 'blog'
  }

  /**
   * ÊòæÁ§∫Êé®ËçêÁªìÊûú
   */
  private showRecommendation(preset: PresetName): void {
    const desc = presetDescriptions[preset]

    const content = document.createElement('div')
    content.style.cssText = 'padding: 30px; text-align: center;'

    content.innerHTML = `
      <div style="font-size: 64px; margin-bottom: 16px;">${desc.icon}</div>
      <h3 style="font-size: 24px; font-weight: 600; margin-bottom: 12px;">${desc.name}</h3>
      <p style="font-size: 16px; color: #6b7280; margin-bottom: 24px;">${desc.description}</p>
      <div style="background: #f0f9ff; border: 1px solid #3b82f6; border-radius: 8px; padding: 16px; margin-bottom: 24px;">
        <p style="font-size: 14px; color: #1e40af;">
          <strong>Êé®ËçêÁêÜÁî±Ôºö</strong><br>
          Âü∫‰∫é‰Ω†ÁöÑÈÄâÊã©ÔºåËøô‰∏™ÈÖçÁΩÆÊúÄÈÄÇÂêà‰Ω†ÁöÑ‰ΩøÁî®Âú∫ÊôØ„ÄÇ
        </p>
      </div>
    `

    const buttonGroup = document.createElement('div')
    buttonGroup.style.cssText = 'display: flex; gap: 12px; justify-content: center;'

    const applyBtn = this.componentFactory.createButton({
      label: 'Â∫îÁî®ÈÖçÁΩÆ',
      type: 'primary',
      onClick: () => {
        this.modal?.hide()
        if (this.onComplete)
          this.onComplete(preset)
      },
    })

    const changeBtn = this.componentFactory.createButton({
      label: 'ÈáçÊñ∞ÈÄâÊã©',
      type: 'secondary',
      onClick: () => {
        this.currentStep = 0
        this.answers = {}
        this.renderStep()
      },
    })

    buttonGroup.appendChild(applyBtn)
    buttonGroup.appendChild(changeBtn)
    content.appendChild(buttonGroup)

    this.modal?.setContent(content)
    this.modal?.setFooter(document.createElement('div'))
  }
}

/**
 * ÊòæÁ§∫ÈÖçÁΩÆÂêëÂØº
 */
export function showConfigWizard(onComplete: (preset: PresetName) => void): ConfigWizard {
  const wizard = new ConfigWizard()
  wizard.show(onComplete)
  return wizard
}
